*{
    margin: 0;
    padding: 0;
 }
 .container{
     max-width: 600px;
     margin: auto;
     padding-left: 10px;
     padding-right: 10px;
 }
 
.ul{
     list-style-type: none;
     display: flex;
     justify-content: space-between;
 }
  .li{
 padding: 0px 5px
 ;}
  .textcolor{
     color: #0000ff;}
 
 
  .content{
     display: flex;
     justify-content: space-between;
     align-items: center;
 }
 
  .fonttype{
     font-family: 'Fascinate Inline', cursive;
 }
 
 .title{
     margin-top: 40px;
 }
 
  .img{
     width: 100%;
     height: 316px;
     display: flex;
     justify-content: center;
     margin-top: 80px;
 }
 
  .creator{
     display: flex;
     justify-content: center;
     margin-top: 80px;
     font-size: 20px;
 }
 
 .enter{
     display: flex;
     margin-top: 40px;
 }
 .inputbtn{
     margin-right: 10px;
     width: 80%;
     height: 40px;
     border: 1px solid grey;
     border-radius: 8px;
     margin-bottom: 7px;}
  .btn{
     background-color: #0000ff;
     color: #fff;
     padding: 6px;
     border-radius: 8px;
     width: 100px;
     border: none;
     margin-bottom: 7px;
 }
 
 .task{
     height: 72px;
     margin-top: 20px;
     background-color: #f6f6f6;
     border-radius: 8px;
     display: flex;
     justify-content: space-between;
     align-items: center;
     padding-right: 5px;
     padding-left: 5px;
     margin-bottom: 10px;
 }
.btntwo{
     background-color: #0000ff;
     color: #fff;
     padding: 6px;
     border-radius: 8px;
     width: 100%;
     border: none;
     display: none;
     position: fixed;
     bottom: 20px;
     margin: 0;
     left: 20px;
     right: 20px;
 }
 .deletbtn{
     font-weight: 100;
     color: red;
     font-size: 20px;
     padding: 5px 10px;
     border-radius: 8px;
     border: red 2px solid;
     margin-right: 4px;
 }
 @media screen and (max-width: 430px){
         .task {
             height: 100px;
             flex-direction: column;
             justify-content: space-around;
         }
          main .btn{
             display: none !important;
         }
          main .btntwo{
             display: block !important;
         }
      main .inputbtn{
         width: 100%;
     }
 }